<script>
export default {
  data() {
    return {
      aimg: [
        { id: 0, tupian: require("../assets/1.png") },
        // { id: 1, tupian: require("../assets/2.png") },
        { id: 1, tupian: require("../assets/3.png") },
        { id: 2, tupian: require("../assets/4.png") }
      ],
      isfiexd: false,
      uname: "",
      phone: "",
      idnumber: "",
      cname: "",
      addressite: ""
    };
  },
  methods: {
    btns() {
      // 验证姓名
      var uname = this.uname;
      if (!/^[\u4E00-\u9FA5]{2,4}$/.test(uname)) {
        this.$message("姓名格式有误！请重新输入");
        return false;
      }
      // / 正则验证手机号  166、198、199号
      var reg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
      var phone = this.phone;
      if (reg.test(phone) == false) {
        this.$message("手机号填写错误");
        return false;
      }
      // 验证身份证
      var idnumber = this.idnumber;
      var sf = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (sf.test(idnumber) == false) {
        this.$message("身份证号填写错误");
        return false;
      }
      // 工作dizhi
      var addressite = this.addressite;
      if (addressite == "" || addressite == null) {
        this.$message("单位地址不能为空");
        return;
      }
      // 验证工作mingchen
      var cname = this.cname;
      if (cname == "" || cname == null) {
        this.$message("单位名称不能为空");
        return
      }
      // 获取时间
      var date = new Date();
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var riqi= year + seperator1 + month + seperator1 + strDate;
      // 发送请求传输数据
      this.axios
        .post("index/api/submit", { uname, phone, idnumber, addressite, cname,riqi})
        .then(res => {
          if(res.data.code==1){
            this.$message({
              message: "提交成功",
              type: "success"
            });
          }else{
            this.$message({
              message: "警告,当前服务器卡顿,请重试",
              type: "warning"
            });
          }
        });
    }
  }
};
</script>

